    // ÏòÅÌôî ÏÉÅÏÑ∏ ÎÇ¥Ïö© Î∂àÎü¨Ïò§Í∏∞
    export default async function getDetail(id = '') {
      return new Promise((resolve, reject) => {
        fetch(`https://omdbapi.com/?apikey=7035c60c&i=${id}&plot=sho`)
        .then(res => res.json())
        .then(res => resolve(res))
        .then(json => {
          if(json.Response === 'False'){
          reject(json.Error)
          }
          resolve(json)
          })
          .catch(error => {
          reject(error)
      })
    })
  };